.account-details{
    background: white;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.1), 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    padding: 8px 0px;
    position: absolute;
    right: 16px;
    display: none;
    animation: fadeIn .3s;
}